<template>
  <div>
    <el-container>
      <el-header>


        <el-row  style="margin-bottom: 5px;" >
          <el-col :span="6" >
            <el-select v-model="currentDB" placeholder="请选择">
              <el-option
                v-for="item in db_options"
                :key="item.value"
                :label="item.label"
                :value="item.value"

              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="18" style="display: flex;">
            <el-button type="primary"  icon="el-icon-refresh" circle @click="queryDB"></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-container  class="new-flag" style="height:100%;border: 1px solid #eee">
        <el-aside     width="400px" style=" height: calc(100vh); background-color: rgb(238, 241, 246)">
          <el-scrollbar style="width:100%; height: 100%;">
              <el-tree
                style=" height: calc(100vh);"
                :data="keysTreeData"

                default-expand-all
                node-key="id"
                ref="tree"
                highlight-current
                :props=" {children: 'detail',label: 'caption' }"
                @node-click="handleTreeNodeClick"

              >
                <template  slot-scope="{ node, data }">
                  <el-tooltip   effect="dark" :content="data.caption" placement="right-end"  slot="label">
                    <span>{{data.caption}}</span>
                  </el-tooltip>
                </template>

              </el-tree>
          </el-scrollbar>


        </el-aside>
        <el-main>
          <el-row  style="margin-bottom: 5px;">
            <el-col :span="2"><div class="grid-content bg-purple" style="line-height: 40px;height: 40px;" >KEY</div></el-col>
            <el-col :span="22"><div class="grid-content bg-purple"><el-input v-model="key">{{this.key}}</el-input></div></el-col>
          </el-row>
          <el-row  style="margin-bottom: 5px;">
            <el-col :span="2"><div class="grid-content bg-purple" style="line-height: 40px;height: 40px;" >TTL</div></el-col>
            <el-col :span="22"><div class="grid-content bg-purple"><el-input v-model="ttl">{{this.ttl}}</el-input></div></el-col>
          </el-row>
          <el-row v-if="this.type==='string'" style="margin-bottom: 5px;" >
<!--          <el-row v-show="this.type==='string'" style="margin-bottom: 5px;" >-->
            <el-col :span="2"><div class="grid-content bg-purple" style="line-height: 40px;height: 40px;" >VALUE</div></el-col>
            <el-col :span="22"><div class="grid-content bg-purple"><el-input v-model="value" type="textarea" rows="20">{{this.value}}</el-input></div></el-col>
          </el-row>
          <el-row v-if="this.type==='string'" style="margin-bottom: 5px;" >
            <el-col :span="2"><div class="grid-content bg-purple" style="line-height: 40px;height: 40px;" ></div></el-col>
            <el-col :span="22"><div class="grid-content bg-purple"><el-button @click="saveConfirm">保存</el-button></div></el-col>
          </el-row>

          <el-row  v-if="this.type==='hash'" style="margin-bottom: 5px;" >
<!--          <el-row  v-show="this.type==='hash'" style="margin-bottom: 5px;" >-->
            <el-col :span="2"><div class="grid-content bg-purple" style="line-height: 40px;height: 40px;" >VALUE</div></el-col>
            <el-col :span="22">
              <div class="grid-content bg-purple">
                <el-table
                  border

                  ref="multipleTable"
                  :data="tableData"
                  tooltip-effect="dark"

                  :row-style="{'height':'10px'}"
                  :cell-style="{'padding':'5px 0px'}"

                  :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' }"
                >
                  <el-table-column
                    type="index"
                    width="50">
                  </el-table-column>
                  <el-table-column width="150" property="k" label="key"></el-table-column>
                  <el-table-column width="500" property="v" label="value"></el-table-column>
                </el-table>
             </div>
            </el-col>
          </el-row>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
name: "NoSQLRWExample",
  methods:{
    handleTreeNodeClick(data, node) {
      console.info("type:"+data.type);
      let that = this;
      if(data.type==="2"){
          this.key = data.caption;
          ////
        let _jsondata_ =   {
          db:this.currentDB,
          key:this.key
        } ;
        //console.info("json:"+JSON.stringify(_jsondata_));

        const loading = this.$loading({
          lock: true,
          text: '查询中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, .7)'
        });

        var p1 = this.customGlobal.postHttpClient(
          //this.customGlobal.serverUrl.postQueryValveCurrentDataList,
          "/fr/postQueryRedisKeyValue",
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){

            loading.close();
            that.value =  res.data.value ;
            that.ttl =  res.data.ttl ;
            that.type = res.data.type;
            if(that.type==="string"){

            }else if(that.type==="hash"){
                that.tableData = JSON.parse(res.data.value);
            }

            console.info(that.ttl );
          }else{
            console.info("查询失败");
            loading.close();


            this.$alert(res.data.msg, '查询失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
              }
            });
          }

        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();

          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();


          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

          ////
      }else{
        console.info("type:"+data.type);
      }
    },
    queryDB(){


      let that = this;
      this.key = "";
      this.value = "";
      this.ttl = "-1";
      let _jsondata_ =   {
        db:this.currentDB
      } ;
      //console.info("json:"+JSON.stringify(_jsondata_));

      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        //this.customGlobal.serverUrl.postQueryValveCurrentDataList,
        "/fr/postQueryRedisDBKeys",
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );

      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.keysTreeData = JSON.parse( res.data.rows );

          console.info(that.keysTreeData );
        }else{
          console.info("查询失败");
          loading.close();

          that.keysTreeData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.keysTreeData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.keysTreeData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    saveConfirm() {
      let that = this;
      this.$confirm('确认要修改['+this.key+']?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,
        //center: true
      }).then(() => {
        that.saveValue()
      }).catch(() => {

      });
    },
    saveValue(){
      let that = this;
      if (that.type==="string"){
        let _jsondata_ =   {
          key:this.key,
          value:this.value,
        } ;
        const loading = this.$loading({
          lock: true,
          text: '更新中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, .7)'
        });

        var p1 = this.customGlobal.postHttpClient(
          //this.customGlobal.serverUrl.postQueryValveCurrentDataList,
          "/fr/postSaveKeyValue",
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){

            this.$message({
                type: 'success',
                showClose: false,
                duration:1000,
                dangerouslyUseHTMLString: true,
                message: "修改成功"
              });
            loading.close();
            console.info(res.data);
          }else{
            console.info("修改失败");
            loading.close();


            this.$alert(res.data.msg, '修改失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
              }
            });
          }

        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();

          this.$alert(JSON.stringify(error.message), '修改出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();


          this.$alert(JSON.stringify(res.message), '修改出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }else{
        this.$alert("不允许修改",'出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }

    }
  },
  data() {
    return {
      type:"string",
      key:"",
      value:"",
      ttl:"-1",
      db_options: [{
        value: '0',
        label: 'db0'
      }, {
        value: '1',
        label: 'db1'
      }, {
        value: '2',
        label: 'db2'
      }, {
        value: '3',
        label: 'db3'
      }, {
        value: '4',
        label: 'db4'
      }],
      currentDB: '0',
      tableData:[],
      keysTreeData:[
        // {id:this.currentDB,caption: this.currentDBCaption, count:this.keyCount,type:'1',detail:[
        //     {id:'100_1',caption:'短信配置',type:'2'},
        //     {id:'100_2',caption:'微信配置',type:'2'}
        //   ]},
      ]
    }
  },
}
</script>

<style scoped>
.new-flag /deep/ .el-aside>.el-scrollbar>.el-scrollbar__wrap {
  overflow-x: scroll !important;
  overflow-y: scroll !important;
}

</style>



